import { createSlice } from "@reduxjs/toolkit";
import John from "../assits/John.jpg";
import Lisa from "../assits/Lisa.jpg";
import Sam from "../assits/Sam.jpg";

let themeslice = createSlice({
  name: "theme",
  initialState: {
    theme: "Bubble",
    title: "",
    ticker: "",
    color: "#0a4cc7",
    mic: true,
    cam: true,
    stopMicCam: false,
    startStatus: true,
    sideBarStatus: false,
    settingBox: false,
    background: "",
    status: "", //Viewer,Host,Moderator
    cover: {},
    layout: "Fill", //Single,Fill,Grid,Main-sub
    ViewerTapMode: "basic", //basic,quiz
    ShowNameUser: false,
    Users: [
      {
        id: 1,
        name: "John Williams",
        main: false,
        inStage: false,
        activeCam: true,
        type: "img",
        src: John,
      },
      {
        id: 2,
        name: "Lisa Wilson        ",
        type: "img",
        main: false,
        inStage: false,
        activeCam: true,
        src: Lisa,
      },
      {
        id: 3,
        name: "Sam Mitchell",
        type: "img",
        main: false,
        inStage: false,
        activeCam: true,
        src: Sam,
      },
      {
        id: 4,
        name: "Oliver Smith",
        type: "img",
        main: false,
        inStage: false,
        activeCam: true,
      },
      {
        id: 5,
        name: "Jack Jones",
        type: "img",
        main: false,
        inStage: false,
        activeCam: true,
      },
      {
        id: 6,
        name: "Harry Williams",
        type: "img",
        main: false,
        inStage: false,
        activeCam: true,
      },
      {
        id: 7,
        name: "Isla Wilson",
        type: "img",
        main: false,
        inStage: false,
        activeCam: true,
      },
      {
        id: 8,
        name: "Ava Evans",
        type: "img",
        main: false,
        inStage: false,
        activeCam: true,
      },
      {
        id: 9,
        name: "Emily Thomas",
        type: "img",
        main: false,
        inStage: false,
        activeCam: true,
      },
      {
        id: 10,
        name: "Mia Hughes",
        type: "img",
        main: false,
        inStage: false,
        activeCam: true,
      },
      {
        id: 11,
        name: "Ahmed",
        type: "img",
        main: false,
        inStage: false,
        activeCam: true,
      },
      {
        id: 12,
        name: "Sophia Clark",
        type: "img",
        main: false,
        inStage: false,
        activeCam: true,
      },
      {
        id: 13,
        name: "Charlotte Green",
        type: "img",
        main: false,
        inStage: false,
        activeCam: true,
      },
      {
        id: 14,
        name: "Lily Wright",
        type: "img",
        main: false,
        inStage: false,
        activeCam: true,
      },
      {
        id: 15,
        name: "Ethan Mitchell",
        type: "img",
        main: false,
        inStage: false,
        activeCam: false,
      },
    ],
    screenMode: "landscape", //Landscape,Portrait
  },
  reducers: {
    getTheme: (state, action) => {
      state.theme = action.payload;
    },
    getTitleBanner: (state, action) => {
      state.title = action.payload;
    },
    getTicker: (state, action) => {
      state.ticker = action.payload;
    },
    getColor: (state, action) => {
      state.color = action.payload;
    },
    MicStatus: (state, action) => {
      state.mic = action.payload;
    },
    CamStatus: (state, action) => {
      state.cam = action.payload;
      state.Users = state.Users.map((user) => ({
        ...user,
        // Set activeCam to false for the user with main: true
        activeCam: user.main ? (action.payload ? true : false) : user.activeCam,
      }));
    },
    StopCameraMic: (state, action) => {
      state.stopMicCam = action.payload;

      state.Users = state.Users.map((user) => ({
        ...user,
        // Set activeCam to false for the user with main: true
        activeCam: user.main ? (action.payload ? false : true) : user.activeCam,
      }));
    },
    MangeStart: (state, action) => {
      state.startStatus = action.payload;
    },
    ManageSideBarSize: (state, action) => {
      state.sideBarStatus = action.payload;
    },
    ManageSettingBox: (state, action) => {
      state.settingBox = action.payload;
    },
    ManageControlStatus: (state, action) => {
      state.status = action.payload;
    },
    ManageCover: (state, action) => {
      state.cover = action.payload;
    },
    getLayout: (state, action) => {
      state.layout = action.payload;
    },
    manageUsers: (state, action) => {
      console.log("action.payload", action.payload);
      state.Users = action.payload;
    },
    ChangeScrennMode: (state, action) => {
      state.screenMode = action.payload;
    },
    getBackGruond: (state, action) => {
      state.background = action.payload;
    },
    ManageViwerTapMode: (state, action) => {
      state.ViewerTapMode = action.payload;
    },
    ShowNameUserFn: (state, action) => {
      state.ShowNameUser = action.payload;
    },
  },
});

export default themeslice.reducer;
export let {
  getTitleBanner,
  ShowNameUserFn,
  getBackGruond,
  getTicker,
  getTheme,
  getColor,
  MicStatus,
  CamStatus,
  ChangeScrennMode,
  StopCameraMic,
  MangeStart,
  ManageSideBarSize,
  ManageSettingBox,
  ManageControlStatus,
  ManageCover,
  ManageViwerTapMode,
  getLayout,
  manageUsers,
} = themeslice.actions;
