import React, { Fragment } from "react";
import Marquee from "react-fast-marquee";

function TitleInsideBox({
  title,
  theme,
  ticker,
  color,
  sacle,
  screenMode,
  userName,
}) {
  return (
    <Fragment>
      {theme === "Bubble" && title ? (
        <div className="them-box">
          <div
            className={`wraper-them-${theme}  ${ticker && "haveTikcer"}`}
            style={{
              transform: `scale(${sacle})`,
              minWidth: screenMode === "portrait" ? "654px" : "1248px",
            }}
          >
            {title && !title.ticker && (
              <div className={`${theme} `} style={{ background: color }}>
                {title && !title.ticker && (
                  <text style={{ fontSize: userName ? "18px" : "31.2px" }}>
                    {title}
                  </text>
                )}
              </div>
            )}
          </div>
        </div>
      ) : theme === "Classic" ? (
        <div className="them-box">
          <div
            className={`wraper-them-${theme}  ${ticker && "haveTikcer"}`}
            style={{
              transform: `scale(${sacle})`,
              minWidth:
                screenMode === "portrait"
                  ? userName
                    ? "200px"
                    : "654px"
                  : userName
                  ? "200px"
                  : "1164.8px",
            }}
          >
            <div className="layer" style={{ background: color }}></div>
            {title && !title.ticker && (
              <div
                className={`${theme} `}
                style={{
                  minHeight: userName ? "50px" : "106px",
                  padding: userName ? "14px" : "6px 60px 6px 50px",
                }}
              >
                {title && !title.ticker && (
                  <text style={{ fontSize: userName ? "18px" : "31.2px" }}>
                    {title}
                  </text>
                )}
              </div>
            )}
          </div>
        </div>
      ) : theme === "Minimal" ? (
        <div className="them-box">
          <div
            className={`wraper-them-${theme} ${ticker && "haveTikcer"}`}
            style={{
              transform: `scale(${sacle})`,
              minWidth: screenMode === "portrait" ? "654px" : "1248px",
            }}
          >
            {title && !title.ticker && (
              <div className="box-Minimal-content">
                <div className="layer"></div>
                <div className={`${theme} `}>
                  {title && !title.ticker && (
                    <text style={{ fontSize: userName ? "18px" : "31.2px" }}>
                      {title}
                    </text>
                  )}
                </div>
              </div>
            )}
            <div className="border-Minimal" style={{ background: color }}></div>
          </div>
        </div>
      ) : (
        <div className="them-box">
          <div
            className={`wraper-them-${theme} ${ticker && "haveTikcer"}`}
            style={{
              transform: `scale(${sacle})`,
              minWidth: screenMode === "portrait" ? "654px" : "1248px",
            }}
          >
            {title && !title.ticker && (
              <div className={`${theme} `} style={{ background: color }}>
                {title && (
                  <text style={{ fontSize: userName ? "18px" : "31.2px" }}>
                    {title}
                  </text>
                )}
              </div>
            )}
          </div>
        </div>
      )}
      {ticker && ticker.ticker && (
        <div
          className="ticker-box"
          style={{
            background: color,
            transform: `scale(${sacle})`,
            minWidth: screenMode === "portrait" ? "654px" : "1248px",
          }}
        >
          <Marquee direction="left">
            <span>{ticker.title}</span>
          </Marquee>
        </div>
      )}
    </Fragment>
  );
}

export default TitleInsideBox;
